import { defaultTheme } from '@splunk/splunk-utils/themes'

const defaultThemeSplunkThemeProviderMap = {
  enterprise: {
    family: 'enterprise',
    colorScheme: 'light',
    density: 'comfortable'
  },
  enterpriseDark: {
    family: 'enterprise',
    colorScheme: 'dark',
    density: 'comfortable'
  },
  lite: {
    family: 'enterprise',
    colorScheme: 'light',
    density: 'comfortable'
  },
  prisma: {
    family: 'prisma',
    colorScheme: 'light',
    density: 'comfortable'
  }
}

const ThemeProviderSettings =
    defaultThemeSplunkThemeProviderMap[defaultTheme()] || defaultThemeSplunkThemeProviderMap.enterprise

export { ThemeProviderSettings }
